import React from "react";
import { useState } from "react";

const Content = () => {
  const [articles, setArticles] = useState([
    { id: 0, imp: "1", cit: "1", nath: "1", order: "1", type: "2" },
  ]);

  const addArticle = () => {
    const oldarticles = [...articles];
    let theid = 0;
    if (oldarticles[oldarticles.length - 1]) {
      theid = oldarticles[oldarticles.length - 1].id + 1;
    }
    const newarticles = [
      ...oldarticles,
      { id: theid, imp: "1", cit: "1", nath: "1", order: "1", type: "1" },
    ];
    setArticles(newarticles);
  };

  const onDeleteItem = (id) => {
    const oldarticles = [...articles];
    const newarticles = oldarticles.filter((item) => {
      return item.id !== id;
    });
    setArticles(newarticles);
  };

  const onChangeValue = (event, id, key) => {
    let oldarticles = [...articles];
    const index = oldarticles.findIndex((item) => {
      return item.id === id;
    });
    console.log(index, id);
    let newOne = oldarticles[parseInt(index)];
    console.log(newOne);
    newOne[key.toString()] = event.target.value;
    oldarticles.splice(index, 1, newOne);
    setArticles(oldarticles);
  };

  const coefficient = 0.1;
  const box1Calc = (imp) => {
    const result = (3 * parseFloat(imp)) / (6 + parseFloat(imp));
    return result;
  };

  const box2Calc = (cit) => {
    const result = (3 * parseInt(cit)) / (50 + parseFloat(cit));
    return result;
  };

  const box3Calc = (author_n) => {
    const bottom = Math.log10(parseInt(author_n));
    const result = 1 / (1 + bottom);
    return result;
  };

  const box4Calc = (order) => {
    const bottom = Math.log10(parseInt(order));
    const result = 1 / (1 + bottom);
    return result;
  };

  const singleCalc = (imp, cit, author_n, order, articleType) => {
    const mainResult =
      coefficient *
      (box1Calc(imp) +
        box2Calc(cit) +
        box3Calc(author_n) +
        box4Calc(order) +
        parseInt(articleType));
    return mainResult;
  };

  const mainCalcFunc = () => {
    //Do Sigma
    let sigma = 0.0;
    articles.forEach((item) => {
      sigma =
        sigma +
        singleCalc(item.imp, item.cit, item.nath, item.order, item.type);
    });
    console.log(sigma);
    document.getElementById("result").innerHTML = parseFloat(sigma);
  };

  const articlesList = articles.map((item) => {
    return (
      <li key={item.id}>
        <div className="listitem listitem-imp">
          <input
            value={item.imp}
            onChange={(e) => onChangeValue(e, item.id, "imp")}
          />
        </div>
        <div className="listitem listitem-cit">
          <input
            value={item.cit}
            onChange={(e) => onChangeValue(e, item.id, "cit")}
          />
        </div>
        <div className="listitem listitem-nath">
          <input
            value={item.nath}
            onChange={(e) => onChangeValue(e, item.id, "nath")}
          />
        </div>
        <div className="listitem listitem-order">
          <input
            value={item.order}
            onChange={(e) => onChangeValue(e, item.id, "order")}
          />
        </div>
        <div className="listitem listitem-type">
          <select
            value={item.type}
            onChange={(e) => onChangeValue(e, item.id, "type")}
          >
            <option value="1">Letter , Editorial , or Case Report</option>
            <option value="2">
              Original Papers , Review , Meta-Analysis , or Book Chapter
            </option>
          </select>
        </div>
        <div className="listitem listitem-tools">
          <div className="delete-item" onClick={() => onDeleteItem(item.id)}>
            Ã—
          </div>
        </div>
      </li>
    );
  });
  return (
    <div className="content">
      <div className="list">
        <li>
          <div className="listitem listitem-imp listitem-header">
            Impact Factor
          </div>
          <div className="listitem listitem-cit listitem-header">
            Number Of Citation
          </div>
          <div className="listitem listitem-nath listitem-header">
            Number Of Authors
          </div>
          <div className="listitem listitem-order listitem-header">
            Order Of Author
          </div>
          <div className="listitem listitem-type listitem-header">
            Article Type
          </div>
          <div className="listitem listitem-tools listitem-header"></div>
        </li>

        {articlesList}
        <div className="add-item">
          <div className="add-btn" onClick={addArticle}>
            +
          </div>
        </div>
      </div>
      <hr />
      <div className="calc" onClick={mainCalcFunc}>
        Calculate U-Index
      </div>
      <div className="result">
        <span>
          Result: <b id="result"></b>
        </span>
      </div>
    </div>
  );
};

export default Content;
